.card {
  border-radius: 2px;
  height: 200px;
  margin: 5px;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  margin-top: 20px;
}

.card > .img-container > img {
  width: 60%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
  padding: 0; 
}

.card > .content > ul > li {
  margin-top: 18px;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
}

.score {
    width: 100%;
    text-align: center;
  }
  

.status {
    width: 100%;
    text-align: center;

  }
  

.instructions {
    width: 100%;
    text-align: center;

  }
  

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

